.container {
  padding: 0 140px;
}
.firstLine {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.firstLine > h1 {
  margin: 0 50px 0 0;
}

h3 {
  font-weight: 400;
}

.secondLine {
  display: flex;
  margin-bottom: 20px;
  cursor: default;
}
.secondLine > h3 {
  margin: 0 40px 0 0;
}

.secondLine > h3:last-child {
  display: flex;
  align-items: center;
}

.iconCont > svg {
  margin-left: 10px;
  color: #c5c6c8;
  cursor: pointer;
}

.iconCont {
  display: flex;
  position: relative;
}

.msgBox {
  display: none;
  position: absolute;
  top: 0;
  left: 35px;
  width: 150px;
  background-color: #202833;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  color: #66fcf1;
  font-size: 14px;
}
.iconCont:hover .msgBox {
  display: block;
}

.genre {
  display: flex;
  margin-bottom: 20px;
}
.genre > h3 {
  margin: 0 20px 0 0;
}

.summary > p {
  font-size: 16px;
  line-height: 24px;
}

/* Extra small devices */
@media only screen and (max-width: 600px) {
  .container {
    padding: 0;
  }
  .secondLine > h3:last-child {
    align-items: center;
  }

  h3 {
    font-size: 14.5px;
  }
  .title {
    font-size: 20px;
  }

  .msgBox {
    top: -10px;
    width: 80px;
  }

  .iconCont > svg {
    margin-left: 7px;
  }
}
